
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  width: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

.home {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
}

.home:before {
  content: '';
  background: url('./assets/imdex-weather.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.home .search {
  text-align: center;
  padding: 1rem 1rem 1rem 0rem;
  display: flex;
}

.home input {
  padding: .7rem 1.5rem;
  font-size: 2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255, 255, 255, 0.4);
  color: white;
}

::placeholder {
  color: #f8f8f8;
}

.container {
  max-width: 700px;
  height: 700px;
  margin: auto;
  padding: 0 1rem;
  padding-top: 20px;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.home .top {
  width: 100%;
  margin: 1rem auto;
}

.home .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.home .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255, 0.2);
}

.bold {
  font-weight: 700;
}

.results {
  font-size: 0.8rem;
  padding-left: 10px;
}

span.checkbox {
  justify-content: flex-start;
}

.css-j204z7-MuiFormControlLabel-root .MuiFormControlLabel-label {
  margin-left: -300px;
}

#password {
  min-width: 320px;
}